import mongoose, { Schema, Document, Model } from 'mongoose';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface INursePaymentDocument extends Document {
  nurse: Schema.Types.ObjectId;
}

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface INursePaymentModel extends Model<INursePaymentDocument> {}

const paymentSchema: Schema = new Schema<INursePaymentDocument>(
  {
    // relations
    nurse: {
      type: Schema.Types.ObjectId,
      required: [true, 'Payment should belong to a nurse!'],
    },
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

const NursePayment: INursePaymentModel = mongoose.model<
  INursePaymentDocument,
  INursePaymentModel
>('NursePayment', paymentSchema);
export default NursePayment;
