import * as jwt from 'jsonwebtoken';
import * as crypto from 'crypto';
import * as CONST from '.';
import { eStatusCode } from '@nursesrent/libs/shared';
import { NextFunction, Request } from 'express';
import mongoose from 'mongoose';
import { AppError, IUrl } from '.';

/**
 *
 * @param id
 * @param next
 * @returns 400 bad request if id its not a mongodb _id
 */
export const checkId = (id: string, next: NextFunction) => {
  if (!mongoose.Types.ObjectId.isValid(id))
    return next(
      new AppError('Please provide a valid id.', eStatusCode.BAD_REQUEST)
    );
};

/**
 *
 * @param {*} user the signed user
 * @returns the signed JWT token with user id as payload
 */
export const signJWTToken = (user) => {
  const token = jwt.sign(
    { id: user._id, email: user.email },
    process.env.JWT_SECRET_KEY,
    {
      expiresIn: process.env.JWT_TOKEN_EXPIRES_IN,
    }
  );

  return token;
};

/**
 *
 * @param {*} user the signed user
 * @param {*} res response generated form the server
 * @param {*} stausCode status code (200 or 201) where 200 is the default one
 * @returns response with the status code and JWT token
 */
export const sendUserTokenSuccess = (user, res, stausCode = eStatusCode.OK) => {
  const token = signJWTToken(user);

  const jwtCookieOptions = {
    expires: new Date(
      Date.now() +
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        (process.env.JWT_COOKIE_EXPIRES_IN as any) * 24 * 60 * 60 * 1000
    ),
    httpOnly: true,
    sameSite: 'lax',
    secure: false,
  };
  if (process.env.NODE_ENV !== CONST.ENV.PROD) jwtCookieOptions.secure = true;
  res.cookie('jwt', token, jwtCookieOptions);

  if (!user.isVerified) {
    return res.status(stausCode).json({
      status: CONST.STATUS.SUCCESS,
      token,
      message: 'Please verify your email account!',
    });
  }

  return res.status(stausCode).json({
    status: CONST.STATUS.SUCCESS,
    token,
  });
};

/**
 *
 * @param {*} tokenToHash pass a token if you need to hash it (if no token is passed a token will be generated ranomly)
 * @returns an object that contains token and hashed token
 */
export const signOrEncryptTokens = async (tokenToHash?: string) => {
  if (!tokenToHash) {
    const generatedToken = crypto.randomBytes(32).toString('hex');

    return {
      token: generatedToken,
      hashedToken: crypto
        .createHash('sha256')
        .update(generatedToken)
        .digest('hex'),
    };
  }
  return {
    token: tokenToHash,
    hashedToken: crypto.createHash('sha256').update(tokenToHash).digest('hex'),
  };
};

/**
 *
 * @param req
 * @returns app url
 */
export const generateUrl = (req: Request): IUrl => {
  return {
    baseFrontUrl: `${req.protocol}://${req.get('host')}`,
    baseApiUrl: `${req.protocol}://${req.get('host')}/api/v1`,
  };
};
