/* eslint-disable @typescript-eslint/no-empty-interface */
// interfaces
export interface HostDto {
  firstName: string;
  lastName: string;
  email: string;
  phone: string;
  dlNumber: number;
  expirationDate: Date;
  state: string;
  isVerified?: boolean;
  isActive?: boolean;
  createdAt?: Date;
  disabled?: boolean;
  responseTime?: string;
  numberOfReports?: number;
  responseRate?: string;
  profilPicture?: string;
  subscription?: string[];
  properties?: string[];
}
export interface CreateHostDto {
  firstName: string;
  lastName: string;
  phone: string;
  email: string;
  password: string;
  passwordConfirm: string;
  dlNumber: number;
  expirationDate: Date;
  state: string;
}
export interface NurseDto {
  personalInfo: {
    firstName?: string;
    lastName?: string;
    displayName: string;
    email: string;
    phone: string;
    dateOfBirth: Date;
    state: string;
    homeTown?: string;
    dreamJob?: string;
    travelWithPet?: boolean;
    about?: string;
  };
  propertyRental?: {
    workExperience?: {
      occupation: eOccupation;
      speciality: eSpeciality;
      favouriteStateToWork: eFavouriteStateToWork;
      certification: string;
      professionalTravelingSince: Date;
      current: Date;
      currentEmployer: string;
    };
    travelingPreferences?: {
      favouriteUnitType: string[];
      transportationMethod: string[];
    };
    reviewAndPreferences?: string;
  };
  favouriteProperties?: {
    myCity?: string;
    topThreeCities?: string[];
  };
  password: string;
  passwordConfirm: string;
  licenceType: eLicenceType;
  licenceNumber: number;
  isVerified?: boolean;
  isActive?: boolean;
  createdAt?: Date;
  disabled?: boolean;
  profilPicture?: string;
  passwordResetToken?: string;
  verificationToken?: string;
  passwordChangetAt?: Date;
  passwordResetExpires?: Date;
  refreshToken?: string;
  reviews?: string[];
  bookings?: string[];
  payments?: string[];
}
export interface CreateNurseDto {
  displayName: string;
  email: string;
  phone: string;
  dateOfBirth: Date;
  password: string;
  passwordConfirm: string;
  licenceType: eLicenceType;
  licenceNumber: number;
  state: string;
}
export interface BookingDto {
  price: number;
  createdAt?: Date;
  bookingDuration: number;
  paid?: boolean;
  nurse: string;
  property: string;
}
export interface CreateBookingDto {}
export interface PropertyDto {
  title: string;
  contactInformation: {
    firstName: string;
    lastName: string;
    email: string;
    phone: string;
  };
  propertyType: {
    type: ePropertyType;
    units: number;
    isSubtle?: boolean;
  };
  location: {
    type: ['Point'];
    coordinates: string[];
    address: string;
    description: string;
  };
  details: {
    bedroom: eBedroom;
    bathroom: eBathroom;
    isBathroomPrivate?: boolean;
    utitlitiesIncluded?: string[];
  };
  minimumDuration?: eMinimumDuration;
  photos: string[];
  description: string;
  amenities?: string[];
  host: HostDto;
  booking?: string;
  reviews?: string[];
}
export interface CreatePropertyDto {}
export interface ReviewDto {
  review: string;
  rating: number;
  createdAt?: Date;
  nurse: NurseDto;
  property: string;
}
export interface CreatePropertyDto {}
export interface SubscriptionDto {
  creditCardNo: string;
  createdAt?: Date;
  lastPayment: number;
  host: string;
}
export interface CreateSubscriptionDto {}
export interface HostPaymentDto {}
export interface CreateHostPaymentDto {}
export interface NursePaymentDto {}
export interface CreateNursePaymentDto {}

