/* eslint-disable @typescript-eslint/no-explicit-any */
import { CreateNurseDto, eStatusCode } from '@nursesrent/libs/shared';
import { promisify } from 'util';
import { Nurse } from '../models';
import * as jwt from 'jsonwebtoken';
import {
  AppError,
  catchAsync,
  Email,
  generateUrl,
  sendUserTokenSuccess,
  signOrEncryptTokens,
} from '../utils';
import * as CONST from '../utils';

const signup = catchAsync(async (req, res, next) => {
  const {
    displayName,
    email,
    dateOfBirth,
    licenceNumber,
    licenceType,
    password,
    passwordConfirm,
    phone,
    state,
  } = req.body as CreateNurseDto;

  const userData = {
    displayName,
    email,
    dateOfBirth,
    phone,
    state,
    licenceNumber,
    licenceType,
    password,
    passwordConfirm,
  };

  // create the user
  const createdUser = new Nurse(userData);
  const user = await createdUser.save();

  if (user) {
    // send welcome email
    const isSuccess = await new Email(
      userData,
      generateUrl(req).baseFrontUrl,
      'Currently we cannot create an account, please try again latter!',
      next
    ).sendWelcome();
    if (!isSuccess) {
      await Nurse.findByIdAndDelete(createdUser.id);
    } else {
      isSuccess && sendUserTokenSuccess(createdUser, res, eStatusCode.CREATED);
    }
  }
});

const sendVerifyAccountEmail = catchAsync(async (req, res, next) => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const { user } = req as any;

  if (user.isVerified) {
    return next(
      new AppError('Users e-mail is not verified!', eStatusCode.UNAUTHORIZED)
    );
  }

  const verificationToken = await user.createverifyToken();
  const baseUrl = generateUrl(req);
  const isSuccess = await new Email(
    user,
    `${baseUrl.baseFrontUrl}/nurse/verify/${verificationToken}`,
    'E-mail sent failed, please try again latter!',
    next
  ).sendEmailVerification();

  if (isSuccess) {
    await user.save({ validateBeforeSave: false });
    if (process.env.NODE_ENV === CONST.ENV.DEV && !user.isVerified) {
      return res.status(eStatusCode.OK).json({
        status: CONST.STATUS.SUCCESS,
        message:
          'We have sent an e-mail with instructions on how to verufy your email account.',
      });
    }
    res.status(eStatusCode.OK).json({
      status: CONST.STATUS.SUCCESS,
      message:
        'We have sent an e-mail with instructions on how to verufy your email account.',
    });
  }
});

const verify = catchAsync(async (req, res, next) => {
  const { token } = req.params;

  const encryptedToken = await signOrEncryptTokens(token);

  const nurse: any = await Nurse.findOne({
    verificationToken: encryptedToken.hashedToken,
  });

  if (!nurse) {
    return next(
      new AppError(
        'Verification failed, please try again!',
        eStatusCode.UNAUTHORIZED
      )
    );
  }

  if (nurse.isVerified) {
    return next(new AppError('This nurse is verified.', eStatusCode.CONFLICT));
  }

  nurse.verificationToken = undefined;
  nurse.isVerified = true;
  await nurse.save({ validateBeforeSave: false });

  res.status(eStatusCode.OK).json({
    status: CONST.STATUS.SUCCESS,
    message: 'Nurse verified successfuly!',
  });
});

const signin = catchAsync(async (req, res, next) => {
  const { email, password } = req.body;

  if (!email || !password) {
    return next(
      new AppError(
        'Please enter your e-mail or password!',
        eStatusCode.BAD_REQUEST
      )
    );
  }

  const nurse = await Nurse.findOne({ email }).select('+password');
  if (!nurse) {
    return next(
      new AppError(
        'E-mail or password is not correct!',
        eStatusCode.BAD_REQUEST
      )
    );
  }
  if (!(await nurse.schema.methods.checkPassword(password, nurse.password))) {
    return next(
      new AppError(
        'E-mail or password is not correct!',
        eStatusCode.BAD_REQUEST
      )
    );
  }
  sendUserTokenSuccess(nurse, res);
});

const logOut = (_req, res) => {
  res.cookie('jwt', 'loggedout', {
    expires: new Date(Date.now() + 10 * 1000),
    httpOnly: true,
  });
  res.status(eStatusCode.OK).json({
    status: CONST.STATUS.SUCCESS,
  });
};

// protect routes controller
const protect = catchAsync(async (req: any, res, next) => {
  let token = '';
  if (
    req.headers.authorization &&
    req.headers.authorization.startsWith('Bearer')
  ) {
    token = req.headers.authorization.split(' ')[1];
  }
  if (req.cookies && req.cookies.jwt) {
    token = req.cookies.jwt;
  }

  if (!token) {
    return next(
      new AppError(
        'You are not logged in, please log in!',
        eStatusCode.UNAUTHORIZED
      )
    );
  }

  const decodedToken: any = await promisify(jwt.verify as any)(
    token,
    process.env.JWT_SECRET_KEY
  );

  const freshNurse: any = await Nurse.findById(decodedToken.id);
  if (!freshNurse) {
    return next(
      new AppError(
        'This token has expired, please login again!',
        eStatusCode.UNAUTHORIZED
      )
    );
  }

  if (freshNurse.passwordChangetAfter(decodedToken.iat)) {
    return next(
      new AppError(
        'This nurset has changed the password lattely, please login again with the new password!',
        eStatusCode.UNAUTHORIZED
      )
    );
  }

  req.user = freshNurse;
  next();
});

// password reset functionality
const forgotPassword = catchAsync(async (req, res, next) => {
  const nurse: any = await Nurse.findOne({ email: req.body.email });
  if (!nurse) {
    return next(
      new AppError(
        'Nurse with this email does not exists!',
        eStatusCode.NOT_FOUND
      )
    );
  }

  if (nurse.isVerified === false) {
    return next(
      new AppError('Please verify your account!', eStatusCode.UNAUTHORIZED)
    );
  }

  const passwordResetToken = await nurse.createPasswordResetToken();
  const baseUrl = generateUrl(req);

  try {
    const isSuccess = await new Email(
      nurse,
      `${baseUrl.baseFrontUrl}/nurse/changepassword/${passwordResetToken}`,
      'Failed to sent e-mail, please try again latter!',
      next
    ).sendPasswordReset();
    if (isSuccess) {
      await nurse.save({ validateBeforeSave: false });
      if (process.env.NODE_ENV === CONST.ENV.DEV) {
        return res.status(eStatusCode.OK).json({
          status: CONST.STATUS.SUCCESS,
          message:
            'We have sent an email with instructions on how to change your password.',
        });
      }

      res.status(eStatusCode.OK).json({
        status: CONST.STATUS.SUCCESS,
        message:
          'We have sent an email with instructions on how to change your password.',
      });
    }
  } catch (err) {
    nurse.passwordResetToken = undefined;
    nurse.passwordResetExpires = undefined;
    await nurse.save({ validateBeforeSave: false });
    return next(
      new AppError(
        'Failed to sent e-mail, please try again latter!',
        eStatusCode.INTERNAL_SERVER_ERROR
      )
    );
  }
});

const resetPassword = catchAsync(async (req, res, next) => {
  const token = await signOrEncryptTokens(req.params.token);

  const nurse = await Nurse.findOne({
    passwordResetToken: token.hashedToken,
    passwordResetExpires: { $gt: Date.now() },
  });

  if (!nurse) {
    return next(new AppError('Token is expired!', eStatusCode.BAD_REQUEST));
  }

  if (nurse.isVerified === false) {
    return next(
      new AppError('Please verify your account!', eStatusCode.UNAUTHORIZED)
    );
  }

  nurse.password = req.body.password;
  nurse.passwordConfirm = req.body.passwordConfirm;
  nurse.passwordResetExpires = undefined;
  nurse.passwordResetToken = undefined;
  await nurse.save();

  sendUserTokenSuccess(nurse, res);
});

// update password even if the user has not forget it
const updatepassword = catchAsync(async (req: any, res, next) => {
  const nurse: any = await Nurse.findById(req.user.id).select('+password');

  if (nurse.isVerified === false) {
    return next(
      new AppError('Please verify your account!', eStatusCode.UNAUTHORIZED)
    );
  }

  const { password, passwordConfirm } = req.body;

  if (!nurse.checkPassword(passwordConfirm, nurse.password)) {
    return next(new AppError('Incorrect password!', eStatusCode.BAD_REQUEST));
  }

  nurse.password = password;
  nurse.passwordConfirm = passwordConfirm;
  await nurse.save();

  sendUserTokenSuccess(nurse, res);
});

// refresh user token
const refresh = catchAsync(async (req: any, res, next) => {
  if (!req.user) {
    return next(
      new AppError(
        'You are not logged in, please login!',
        eStatusCode.UNAUTHORIZED
      )
    );
  }
  const freshNurse = await Nurse.findById(req.user.id);
  freshNurse.save({ validateBeforeSave: false });

  if (!freshNurse) {
    return next(new AppError('You are not logged in, please login!', 401));
  }

  sendUserTokenSuccess(freshNurse, res);
});

export const nurseauth = {
  signup,
  sendVerifyAccountEmail,
  verify,
  signin,
  logOut,
  protect,
  forgotPassword,
  resetPassword,
  updatepassword,
  refresh,
};
