import mongoose, { Schema, Document, Model } from 'mongoose';

export interface IBookingDocument extends Document {
  price: number;
  createdAt?: Date;
  bookingDuration: number;
  paid?: boolean;
  nurse: Schema.Types.ObjectId;
  property: Schema.Types.ObjectId;
}

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface IBookingModel extends Model<IBookingDocument> {}

const bookingSchema: Schema = new Schema<IBookingDocument>(
  {
    price: {
      type: Number,
      require: [true, 'Booking must have a price!'],
    },
    createdAt: {
      type: Date,
      default: Date.now(),
    },
    bookingDuration: {
      type: Number,
      required: [true, 'Booking must have a duration!'],
    },
    paid: {
      type: Boolean,
      default: true,
    },

    // relations
    nurse: {
      type: Schema.Types.ObjectId,
      ref: 'Nurse',
      required: [true, 'A booking must have been made by a nurse!'],
    },
    property: {
      type: Schema.Types.ObjectId,
      ref: 'Property',
      required: [true, 'A property should be booked!'],
    },
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

const Booking = mongoose.model<IBookingDocument, IBookingModel>(
  'Booking',
  bookingSchema
);
export default Booking;
