import mongoose, { Model, Schema, Document } from 'mongoose';

export interface ISubscriptionDocument extends Document {
  creditCardNo: string;
  createdAt?: Date;
  lastPayment: number;
  host: Schema.Types.ObjectId;
}

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface ISubscriptionModel extends Model<ISubscriptionDocument> {}

const subscriptionSchema: Schema = new Schema<ISubscriptionDocument>(
  {
    creditCardNo: {
      type: String,
      required: [true, 'Subscription must have a credit card linked!'],
      trim: true,
    },
    createdAt: {
      type: Date,
      default: Date.now(),
    },
    lastPayment: {
      type: Number,
      required: [true, 'A subscription must specify the last payment!'],
    },

    // relations
    host: {
      type: mongoose.Schema.Types.ObjectId,
      ref: 'Host',
      required: [true, 'A subscription must be from a host user!'],
    },
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

const Subscription: ISubscriptionModel = mongoose.model<
  ISubscriptionDocument,
  ISubscriptionModel
>('Subscription', subscriptionSchema);
export default Subscription;
