//core modules/////////////////////////////////////////////////////////
import * as path from 'path';

//npm modules//////////////////////////////////////////////////////////
import * as express from 'express';
import * as logger from 'morgan';
import * as cors from 'cors';
import * as cookieParser from 'cookie-parser';
import * as hpp from 'hpp';
import * as mongoSanitize from 'express-mongo-sanitize';
import * as xss from 'xss-clean';
import rateLimit, { RateLimitRequestHandler } from 'express-rate-limit';
import helmet from 'helmet';

// my modules//////////////////////////////////////////////////////////
import * as CONST from '../utils/constants';
import { eApiRoutes, eStatusCode } from '@nursesrent/libs/shared';
import { AppError } from '../utils/appError';
import { globalErrorHandler } from '../controllers';

// routers//////////////////////////////////////////////////////////////
import { hostRouter, nurseRouter, propertyRouter } from '../routes';

// init app/////////////////////////////////////////////////////////////
const app = express();
const limiter: RateLimitRequestHandler = rateLimit({
  max: 100,
  windowMs: 60 * 60 * 1000,
  message: 'To many request from this IP, please try again in one hour!',
});

// middlewares///////////////////////////////////////////////////////////
app.use(helmet()); // set security HTTP headers
process.env.NODE_ENV === CONST.ENV.DEV && app.use(logger('dev'));
app.use('/assets', express.static(path.join(__dirname, 'assets')));
// app.set('view engine', 'ejs');
// app.set('views', path.join(__dirname, '/assets/views'));
app.use(cors({ origin: true, credentials: true }));
app.use(`${CONST.API_ROUTE}`, limiter); // limit requests from the same api
app.use(express.json());
app.use(cookieParser());
app.use(mongoSanitize()); // Data sanitization againts NsSQL injection
app.use(xss()); // Data sanitization againts XSS
app.use(hpp()); // Prevent parameterr pollution

// routes/////////////////////////////////////////////////////////////////
app.get('/', (req, res) => {
  res.status(eStatusCode.OK).json({
    status: CONST.STATUS.SUCCESS,
    message: 'Welcome to nurses rent api',
  });
});
app.get(eApiRoutes.BASE, (req, res) => {
  res.status(eStatusCode.OK).json({
    status: 'success',
    data: 'welcome to nurses rent api',
  });
});
app.use(`${eApiRoutes.BASE}/${eApiRoutes.PROPERTIES}`, propertyRouter);
app.use(`${eApiRoutes.BASE}/${eApiRoutes.HOST}`, hostRouter);
app.use(`${eApiRoutes.BASE}/${eApiRoutes.NURSE}`, nurseRouter);

app.all('*', (req, res, next) => {
  next(
    new AppError(
      `Can't find ${req.originalUrl} on this server!`,
      eStatusCode.NOT_FOUND
    )
  );
});
// global error handler middleware
app.use(globalErrorHandler);

export default app;
