import { eStatusCode } from '@nursesrent/libs/shared';
import { AppError } from '../utils';
import * as CONST from '../utils';

/* eslint-disable @typescript-eslint/no-explicit-any */
const handleCastErrorDB = (err) => {
  const message = `Invalid ${err.path}: ${err.value}.`;
  return new AppError(message, eStatusCode.BAD_REQUEST);
};

const handleDuplicateFieldsDB = (err) => {
  const value = err.message.match(/(["'])(\\?.)*?\1/)[0];

  const message = `Duplicate field value: ${value}. Please use another value!`;
  return new AppError(message, eStatusCode.CONFLICT);
};

const handleValidationErrorDB = (err: {
  errors: { [s: string]: unknown } | ArrayLike<unknown>;
}) => {
  const errors = Object.values(err.errors).map((el: any) => el.message);

  const message = `Invalid input data. ${errors.join('. ')}`;
  return new AppError(message, eStatusCode.CONFLICT);
};

const handleJWTError = () =>
  new AppError('Invalid token. Please log in again!', eStatusCode.UNAUTHORIZED);

const handleJWTExpiredError = () =>
  new AppError(
    'Your token has expired! Please log in again.',
    eStatusCode.UNAUTHORIZED
  );

const sendErrorDev = (err: any, req: any, res: any) => {
  const error = new AppError(err.message, err.statusCode);
  // A) API
  if (req.originalUrl.startsWith('/api')) {
    return res.status(err.statusCode).json({
      status: error.status,
      error,
      statusCode: err.statusCode,
      message: error.message,
      stack: error.stack,
    });
  }

  // B) RENDERED WEBSITE
  console.error('ERROR ðŸ’¥', err);
  return res.status(err.statusCode).render('error', {
    title: 'Something went very wrong!',
    statusCode: err.statusCode,
    msg: err.message,
  });
};

const sendErrorProd = (err: any, req: any, res: any) => {
  // A) API
  if (req.originalUrl.startsWith('/api')) {
    // A) Operational, trusted error: send message to client
    if (err.isOperational) {
      return res.status(err.statusCode).json({
        status: err.status,
        statusCode: err.statusCode,
        message: err.message,
      });
    }
    // B) Programming or other unknown error: don't leak error details
    // 1) Log error
    console.error('ERROR ðŸ’¥', err);
    // 2) Send generic message
    return res.status(eStatusCode.INTERNAL_SERVER_ERROR).json({
      status: 'error',
      statusCode: eStatusCode.INTERNAL_SERVER_ERROR,
      message: 'Something went very wrong!',
    });
  }

  // B) Programming or other unknown error: don't leak error details
  // 1) Log error
  console.error('ERROR ðŸ’¥', err);
  // 2) Send generic message
  return res.status(err.statusCode).render('error', {
    title: 'Something went very wrong!',
    statusCode: err.statusCode,
    msg: 'Please try again later.',
  });
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export const globalErrorHandler = (
  err: any,
  req: any,
  res: any,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  next: any
) => {
  err.statusCode = err.statusCode || eStatusCode.INTERNAL_SERVER_ERROR;
  err.status = err.status || CONST.STATUS.ERROR;

  if (process.env.NODE_ENV === CONST.ENV.DEV) {
    sendErrorDev(err, req, res);
  } else if (process.env.NODE_ENV === CONST.ENV.PROD) {
    let error = { ...err };
    error.message = err.message;
    error.name = err.name;
    if (error.name === CONST.ERRORTYPES.CASTERROR)
      error = handleCastErrorDB(error);
    if (error.code === CONST.ERRORTYPES.DUBLICATEFIELDERROR)
      error = handleDuplicateFieldsDB(error);
    if (error.name === CONST.ERRORTYPES.VALIDATIONERROR)
      error = handleValidationErrorDB(error);
    if (error.name === CONST.ERRORTYPES.JSONWEBTOKENERROR)
      error = handleJWTError();
    if (error.name === CONST.ERRORTYPES.TOKENEXPIREDERROR)
      error = handleJWTExpiredError();

    sendErrorProd(error, req, res);
  }
};
