import { eApiRoutes } from '@nursesrent/libs/shared';
import { Router } from 'express';
import {
  nurseauth,
  // nurseController
} from '../controllers';

const router = Router();

// auth routes
router.post(eApiRoutes.SIGNUP, nurseauth.signup);
router.post(
  eApiRoutes.VERIFYACCOUNT,
  nurseauth.protect,
  nurseauth.sendVerifyAccountEmail
);
router.post(
  `${eApiRoutes.VERIFYACCOUNT}/:token`,
  nurseauth.protect,
  nurseauth.verify
);
router.post(eApiRoutes.SIGNIN, nurseauth.signin);
router.post(eApiRoutes.REFRESHUSER, nurseauth.protect, nurseauth.refresh);
router.post(eApiRoutes.FORGOTPASSWORD, nurseauth.forgotPassword);
router.post(`${eApiRoutes.RESETPASSWORD}/:token`, nurseauth.resetPassword);
router.post(
  eApiRoutes.UPDATEPASSWORD,
  nurseauth.protect,
  nurseauth.updatepassword
);
router.post(eApiRoutes.LOGOUT, nurseauth.logOut);
