// export interface IPropertyModel extends Model<IPropertyDocument> {}

const propertySchema: Schema = new Schema(
  {
    title: {
      type: String,
      required: [true, 'Property must have a title!'],
      trim: true,
    },
    contactInformation: {
      firstName: {
        type: String,
        required: [true, 'Property must have a contact first name!'],
        trim: true,
      },
      lastName: {
        type: String,
        required: [true, 'Property must have a contact last name!'],
        trim: true,
      },
      email: {
        type: String,
        required: [true, 'Property must have a contact email!'],
        lowercase: true,
        validate: [
          validator.isEmail,
          'Property must have a valid contact email!',
        ],
      },
      phone: {
        type: String,
        required: [true, 'Property must have a contact phone number!'],
        validate: [
          validator.isMobilePhone,
          'Property must have a valid contact phone number!',
        ],
      },
    },
    propertyType: {
      type: {
        type: String,
        required: [true, 'Property must have a type!'],
        enum: ePropertyType,
      },
      units: {
        type: Number,
        required: [true, 'Property must have units specified!'],
      },
      isSubtle: {
        type: Boolean,
        default: false,
      },
    },
    location: {
      type: {
        type: String,
        default: 'Point',
        enum: ['Point'],
      },
      coordinates: {
        type: [Number],
        required: [true, 'Property must have location coordinates!'],
      },
      address: {
        type: String,
        required: [true, 'Property must have a location address!'],
      },
      description: {
        type: String,
        required: [true, 'Property must have a location description!'],
      },
    },
    details: {
      bedroom: {
        type: String,
        required: [true, 'Property must have bedrooms informations!'],
        enum: eBedroom,
      },
      bathroom: {
        type: String,
        required: [true, 'Property must have a bathrooms informations!'],
        enum: eBathroom,
      },
      isBathroomPrivate: {
        type: Boolean,
        default: false,
      },
      utitlitiesIncluded: [String],
    },
    minimumDuration: {
      type: Number,
      default: 1,
      enum: eMinimumDuration,
    },
    photos: {
      type: [String],
      required: [true, 'Some property photos are required!'],
    },
    description: {
      type: String,
      required: [true, 'Property must have a description!'],
    },
    amenities: [String],
    // payment: {},

    // relations
    host: {
      type: Schema.Types.ObjectId,
      ref: 'Host',
      required: [true, 'A property must be created by a host user!'],
    },
    booking: {
      type: Schema.Types.ObjectId,
      ref: 'Booking',
    },
    reviews: [
      {
        type: Schema.Types.ObjectId,
        ref: 'Review',
      },
    ],
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

const Property = mongoose.model('Property', propertySchema);
// const Property: IPropertyModel = mongoose.model<
//   IPropertyDocument,
//   IPropertyModel
// >('Property', propertySchema);
export default Property;
