/* eslint-disable @typescript-eslint/no-unused-vars */
import { PropertyDto } from '@nursesrent/libs/shared';
import { Property } from '../models';
import { handlerFactory } from '.';

const create = handlerFactory.createOne<any, PropertyDto>(Property);
const getAll = handlerFactory.getAll<any, PropertyDto>(Property);
const getOne = handlerFactory.getOne<PropertyDto>(Property);
const updateOne = handlerFactory.updateOne<any, PropertyDto>(Property);
const deleteOne = handlerFactory.deleteOne<any>(Property);

export default {
  getAll,
  create,
  getOne,
  updateOne,
  deleteOne,
};
