import { NursePaymentDto } from '@nursesrent/libs/shared';
import { handlerFactory } from '.';
import { NursePayment } from '../models';
import { INursePaymentDocument } from '../models/nursePayment.model';

const create = handlerFactory.createOne<INursePaymentDocument, NursePaymentDto>(
  NursePayment
);
const getAll = handlerFactory.getAll<INursePaymentDocument, NursePaymentDto>(
  NursePayment
);
const getOne = handlerFactory.getOne<NursePaymentDto>(NursePayment);
const updateOne = handlerFactory.updateOne<
  INursePaymentDocument,
  NursePaymentDto
>(NursePayment);
const deleteOne = handlerFactory.deleteOne<INursePaymentDocument>(NursePayment);

export default {
  getAll,
  create,
  getOne,
  updateOne,
  deleteOne,
};
