import mongoose, { Model, Schema, Document } from 'mongoose';

export interface IReviewDocument extends Document {
  review: string;
  rating: number;
  createdAt?: Date;
  nurse: Schema.Types.ObjectId;
  property: Schema.Types.ObjectId;
}

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface IReviewModel extends Model<IReviewDocument> {}

const reviewSchema: Schema = new Schema<IReviewDocument>(
  {
    review: {
      type: String,
      required: [true, 'Review cannot be empty.'],
      trim: true,
    },
    rating: {
      type: Number,
      min: 1,
      max: 5,
    },
    createdAt: {
      type: Date,
      default: Date.now(),
    },

    // relations
    nurse: {
      type: mongoose.Schema.Types.ObjectId,
      ref: 'Nurse',
      required: [true, 'Reviw must be from a nurse!'],
    },
    property: {
      type: mongoose.Schema.Types.ObjectId,
      ref: 'Property',
      required: [true, 'Review must be writed for a property!'],
    },
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

const Review: IReviewModel = mongoose.model<IReviewDocument, IReviewModel>(
  'Review',
  reviewSchema
);
export default Review;
