/* eslint-disable @typescript-eslint/no-explicit-any */
import * as nodemailer from 'nodemailer';
import { createTransport } from 'nodemailer';
import * as CONST from '.';
import { NextFunction } from 'express';
import { AppError } from '.';
import { eStatusCode } from '@nursesrent/libs/shared';

class Email {
  to: string;
  firstname: string;
  lastname: string;
  url: string;
  from: string;
  isSuccess: boolean;
  next: NextFunction;
  errorMessage: string;
  constructor(
    user: any,
    url: string,
    errorMessage: string,
    next: NextFunction
  ) {
    this.to = user.email;
    this.firstname = user.firstName;
    this.lastname = user.lastName;
    this.url = url;
    this.from = `Nurses rent <${process.env.EMAIL_FROM}>`;
    this.next = next;
    this.isSuccess = false;
    this.errorMessage = errorMessage;
  }

  transporter() {
    if (process.env.NODE_ENV === CONST.ENV.PROD) {
      return (nodemailer as any).createTransport({
        service: 'SendGrid',
        port: process.env.SENDGRID_PORT,
        auth: {
          user: process.env.SENDGRID_USERNAME,
          pass: process.env.SENDGRID_PASSWORD,
        },
      });
    }
    return createTransport({
      host: process.env.EMAIL_HOST as string,
      port: process.env.EMAIL_PORT as any,
      secure: false,
      auth: {
        user: process.env.EMAIL_USERNAME,
        pass: process.env.EMAIL_PASSWORD,
      },
    });
  }

  async send(template: string, subject: string) {
    // let html: any;
    // ejs.renderFile(
    //   path.join(__dirname, `../nursesrent-api/src/views/Email/${template}.ejs`),
    //   {
    //     firstname: this.firstname,
    //     lastname: this.lastname,
    //     to: this.to,
    //     from: this.from,
    //     url: this.url,
    //     subject,
    //   },
    //   // eslint-disable-next-line @typescript-eslint/no-unused-vars
    //   function (err, str) {
    //     if (err) {
    //       // eslint-disable-next-line no-console
    //       console.log(err);
    //       return this.next(
    //         new AppError(
    //           'Can not render the email html',
    //           eStatusCode.INTERNAL_SERVER_ERROR
    //         )
    //       );
    //     }
    //     html = str;
    //   }
    // );
    const html = `<a href=${this.url}>${this.url}</a>`;
    const mailOptions = {
      from: this.from,
      to: this.to,
      subject,
      html,
      // text: (htmlToText as any).fromString(html),
    };

    await this.transporter()
      .sendMail(mailOptions)
      .then(() => (this.isSuccess = true))
      .catch(() => {
        this.isSuccess = false;
        this.next(
          new AppError(this.errorMessage, eStatusCode.INTERNAL_SERVER_ERROR)
        );
      });
  }

  // Email types
  async sendWelcome(): Promise<boolean> {
    await this.send('welcomeEmail', `Hello and welcome to Nurses rent!`);
    return this.isSuccess;
  }

  async sendPasswordReset(): Promise<boolean> {
    await this.send('passwordReset', 'Change your pasword.');
    return this.isSuccess;
  }

  async sendEmailVerification(): Promise<boolean> {
    await this.send('emailVerification', 'Please verify your email!');
    return this.isSuccess;
  }
}

export default Email;
