import { ReviewDto } from '@nursesrent/libs/shared';
import { handlerFactory } from '.';
import { Review } from '../models';
import { IReviewDocument } from '../models/review.model';

const create = handlerFactory.createOne<IReviewDocument, ReviewDto>(Review);
const getAll = handlerFactory.getAll<IReviewDocument, ReviewDto>(Review);
const getOne = handlerFactory.getOne<ReviewDto>(Review);
const updateOne = handlerFactory.updateOne<IReviewDocument, ReviewDto>(Review);
const deleteOne = handlerFactory.deleteOne<IReviewDocument>(Review);

export default {
  getAll,
  create,
  getOne,
  updateOne,
  deleteOne,
};
