import mongoose, { Schema, Document, Model } from 'mongoose';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface IHostPaymentDocument extends Document {
  host: Schema.Types.ObjectId;
}

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface IHostPaymentModel extends Model<IHostPaymentDocument> {}

const paymentSchema: Schema = new Schema<IHostPaymentDocument>(
  {
    // relations
    host: {
      type: Schema.Types.ObjectId,
      required: [true, 'Payment should belong to a nurse!'],
    },
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

const HostPayment: IHostPaymentModel = mongoose.model<
  IHostPaymentDocument,
  IHostPaymentModel
>('HostPayment', paymentSchema);
export default HostPayment;
