/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  eFavouriteStateToWork,
  eLicenceType,
  eOccupation,
  eSpeciality,
} from '@nursesrent/libs/shared';
import mongoose, { Schema, Document, Model } from 'mongoose';
import validator from 'validator';
import * as bcrypt from 'bcryptjs';
import { v4 as uuidv4 } from 'uuid';
import { signOrEncryptTokens } from '../utils';

export interface INurseDocument extends Document {
  firstName?: string;
  lastName?: string;
  displayName: string;
  email: string;
  phone: string;
  dateOfBirth: Date;
  state: string;
  homeTown?: string;
  dreamJob?: string;
  travelWithPet?: boolean;
  about?: string;
  propertyRental?: {
    workExperience?: {
      occupation: eOccupation;
      speciality: eSpeciality;
      favouriteStateToWork: eFavouriteStateToWork;
      certification: string;
      professionalTravelingSince: Date;
      current: Date;
      currentEmployer: string;
    };
    travelingPreferences?: {
      favouriteUnitType: string[];
      transportationMethod: string[];
    };
    reviewAndPreferences?: string;
  };
  favouriteProperties?: {
    myCity?: string;
    topThreeCities?: string[];
  };
  password: string;
  passwordConfirm: string;
  licenceType: eLicenceType;
  licenceNumber: number;
  isVerified?: boolean;
  isActive?: boolean;
  createdAt?: Date;
  disabled?: boolean;
  profilPicture?: string;
  passwordResetToken?: string;
  verificationToken?: string;
  passwordChangetAt?: Date;
  passwordResetExpires?: Date;
  refreshToken?: string;
  reviews?: Schema.Types.ObjectId[];
  bookings?: Schema.Types.ObjectId[];
  payments?: Schema.Types.ObjectId[];
  passwordChangetAfter: (JwtTimestamp: number) => boolean;
  createPasswordResetToken: () => string;
  checkPassword: (
    candidatePassword: string,
    password: string
  ) => Promise<boolean>;
}

export interface INurseModel extends Model<INurseDocument> {
  checkPassword: (
    candidatePassword: string,
    password: string
  ) => Promise<boolean>;
}

const nurseSchema: Schema = new Schema(
  {
    displayName: {
      type: String,
      required: [true, 'Please enter your first name!'],
      trim: true,
    },
    email: {
      type: String,
      required: [true, 'An email address is required!'],
      unique: true,
      lowercase: true,
      validate: [validator.isEmail, 'Please eneter a valid email address!'],
    },
    phone: {
      type: String,
      required: [true, 'A phone number is reuired!'],
      validate: [
        validator.isMobilePhone,
        'Please eneter a valid phone number!',
      ],
    },
    dateOfBirth: {
      type: Date,
      required: [true, 'Please specify date of birth!'],
    },
    state: {
      type: String,
      required: [true, 'Please specify the state!'],
    },
    firstName: String,
    lastName: String,
    homeTown: String,
    dreamJob: String,
    travelWithPet: Boolean,
    about: String,
    propertyRental: {
      workExperience: {
        occupation: {
          type: String,
          enum: eOccupation,
        },
        speciality: {
          type: String,
          enum: eSpeciality,
        },
        favouriteStateToWork: {
          type: String,
          enum: eFavouriteStateToWork,
        },
        certification: String,
        professionalTravelingSince: Date,
        current: Date,
        currentEmployer: String,
      },
      travelingPreferences: {
        favouriteUnitType: [String],
        transportationMethod: [String],
      },
      reviewAndPreferences: String,
    },
    favouriteProperties: {
      myCity: String,
      topThreeCities: [String],
    },
    password: {
      type: String,
      required: [true, 'Please enter your password!'],
      minlength: [8, 'Password should be at least 8 characters long!'],
      select: false,
    },
    passwordConfirm: {
      type: String,
      required: [true, 'Please confirn your password!'],
      validate: {
        validator: function (passwordConfirm) {
          return passwordConfirm === this.password;
        },
        message: 'Passwords are not the same!',
      },
    },
    licenceType: {
      type: String,
      required: [true, 'Nurse must have a licence type!'],
      enum: eLicenceType,
    },
    licenceNumber: {
      type: Number,
      required: [true, 'Nurse must have a licence number!'],
    },
    isVerified: {
      type: Boolean,
      default: false,
    },
    isActive: {
      type: Boolean,
      default: true,
      select: false,
    },
    createdAt: {
      type: Date,
      default: Date.now(),
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    profilPicture: String,
    passwordResetToken: String,
    verificationToken: String,
    passwordChangetAt: Date,
    passwordResetExpires: Date,
    refreshToken: String,

    // relations
    reviews: [
      {
        type: Schema.Types.ObjectId,
        ref: 'Review',
      },
    ],
    bookings: [
      {
        type: Schema.Types.ObjectId,
        ref: 'Booking',
      },
    ],
    payments: [
      {
        type: Schema.Types.ObjectId,
        ref: 'NursePayment',
      },
    ],
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

// pre save middlewaree for changing passwordChangetAt field to the date where password is changed
nurseSchema.pre('save', function (next) {
  if (!this.isModified('password') || this.isNew) return next();

  this.passwordChangetAt = (Date.now() - 1000) as any;
  next();
});

// pre save middleware for hashing and persisting the hashed password to the database
nurseSchema.pre('save', async function (next) {
  if (!this.isModified('password')) return next();

  const password = await bcrypt.hash(this.password, 12);
  this.password = password;
  this.passwordConfirm = undefined;

  next();
});

nurseSchema.pre('save', async function (next) {
  const randomToken: string = uuidv4();
  const refreshToken = await bcrypt.hash(randomToken, 8);
  this.refreshToken = refreshToken;

  next();
});

nurseSchema.methods.checkPassword = async function (
  candidatePassword: string,
  password: string
) {
  return bcrypt.compareSync(candidatePassword, password);
};

nurseSchema.methods.passwordChangetAfter = function (JwtTimestamp: number) {
  if (this.passwordChangetAt) {
    const changedTimestamp = parseInt(
      (this.passwordChangetAt.getTime() / 1000) as any,
      10
    );
    return JwtTimestamp < changedTimestamp;
  }
  return false;
};

nurseSchema.methods.createPasswordResetToken = async function () {
  const resetToken = await signOrEncryptTokens();

  this.passwordResetToken = resetToken.hashedToken;
  this.passwordResetExpires = Date.now() + 10 * 60 * 1000;

  return resetToken.token;
};

nurseSchema.methods.createverifyToken = async function () {
  const verifyToken = await signOrEncryptTokens();

  this.verificationToken = verifyToken.hashedToken;

  return verifyToken.token;
};

const Nurse: INurseModel = mongoose.model<INurseDocument, INurseModel>(
  'Nurse',
  nurseSchema
);
export default Nurse;
