import { HostDto } from '@nursesrent/libs/shared';
import { handlerFactory } from '.';
import Host, { IHostDocument } from '../models/host.model';

const create = handlerFactory.createOne<IHostDocument, HostDto>(Host);
const getAll = handlerFactory.getAll<IHostDocument, HostDto>(Host);
const getOne = handlerFactory.getOne<HostDto>(Host);
const updateOne = handlerFactory.updateOne<IHostDocument, HostDto>(Host);
const deleteOne = handlerFactory.deleteOne<IHostDocument>(Host);

export default {
  getAll,
  create,
  getOne,
  updateOne,
  deleteOne,
};
