import { HostPaymentDto } from '@nursesrent/libs/shared';
import { handlerFactory } from '.';
import HostPayment, { IHostPaymentDocument } from '../models/hostPayment.model';

const create = handlerFactory.createOne<IHostPaymentDocument, HostPaymentDto>(
  HostPayment
);
const getAll = handlerFactory.getAll<IHostPaymentDocument, HostPaymentDto>(
  HostPayment
);
const getOne = handlerFactory.getOne<HostPaymentDto>(HostPayment);
const updateOne = handlerFactory.updateOne<
  IHostPaymentDocument,
  HostPaymentDto
>(HostPayment);
const deleteOne = handlerFactory.deleteOne<IHostPaymentDocument>(HostPayment);

export default {
  getAll,
  create,
  getOne,
  updateOne,
  deleteOne,
};
