import { eApiRoutes } from '@nursesrent/libs/shared';
import { Router } from 'express';
import {
  hostauth,
  // hostController
} from '../controllers';

const router = Router();

// auth routes
router.post(eApiRoutes.SIGNUP, hostauth.signup);
router.post(
  eApiRoutes.VERIFYACCOUNT,
  hostauth.protect,
  hostauth.sendVerifyAccountEmail
);
router.post(
  `${eApiRoutes.VERIFYACCOUNT}/:token`,
  hostauth.protect,
  hostauth.verify
);
router.post(eApiRoutes.SIGNIN, hostauth.signin);
router.post(eApiRoutes.REFRESHUSER, hostauth.protect, hostauth.refresh);
router.post(eApiRoutes.FORGOTPASSWORD, hostauth.forgotPassword);
router.post(`${eApiRoutes.RESETPASSWORD}/:token`, hostauth.resetPassword);
router.post(
  eApiRoutes.UPDATEPASSWORD,
  hostauth.protect,
  hostauth.updatepassword
);
router.post(eApiRoutes.LOGOUT, hostauth.logOut);
