import { BookingDto } from '@nursesrent/libs/shared';
import { handlerFactory } from '.';
import Booking, { IBookingDocument } from '../models/booking.model';

const create = handlerFactory.createOne<IBookingDocument, BookingDto>(Booking);
const getAll = handlerFactory.getAll<IBookingDocument, BookingDto>(Booking);
const getOne = handlerFactory.getOne<BookingDto>(Booking);
const updateOne = handlerFactory.updateOne<IBookingDocument, BookingDto>(
  Booking
);
const deleteOne = handlerFactory.deleteOne<IBookingDocument>(Booking);

export default {
  getAll,
  create,
  getOne,
  updateOne,
  deleteOne,
};
