import { NurseDto } from '@nursesrent/libs/shared';
import { handlerFactory } from '.';
import Nurse, { INurseDocument } from '../models/nurse.model';

const create = handlerFactory.createOne<INurseDocument, NurseDto>(Nurse);
const getAll = handlerFactory.getAll<INurseDocument, NurseDto>(Nurse);
