/* eslint-disable @typescript-eslint/no-explicit-any */
import mongoose, { Schema, Document, Model } from 'mongoose';
import validator from 'validator';
import * as bcrypt from 'bcryptjs';
import { v4 as uuidv4 } from 'uuid';
import { signOrEncryptTokens } from '../utils';

export interface IHostDocument extends Document {
  firstName: string;
  lastName: string;
  email: string;
  phone: string;
  password: string;
  passwordConfirm: string;
  dlNumber: number;
  expirationDate: Date;
  state: string;
  isVerified?: boolean;
  isActive?: boolean;
  createdAt?: Date;
  disabled?: boolean;
  responseTime?: string;
  numberOfReports?: number;
  responseRate?: string;
  profilPicture?: string;
  passwordResetToken?: string;
  verificationToken?: string;
  passwordChangetAt?: Date;
  passwordResetExpires?: Date;
  refreshToken?: string;
  subscription?: Schema.Types.ObjectId;
  properties?: Schema.Types.ObjectId[];
  payments?: Schema.Types.ObjectId[];
  passwordChangetAfter: (JwtTimestamp: number) => boolean;
  createPasswordResetToken: () => string;
  checkPassword: (
    candidatePassword: string,
    password: string
  ) => Promise<boolean>;
}

export interface IHostModel extends Model<IHostDocument> {
  checkPassword: (
    candidatePassword: string,
    password: string
  ) => Promise<boolean>;
}

const hostSchema: Schema = new Schema<IHostDocument>(
  {
    firstName: {
      type: String,
      required: [true, 'Please enter your first name.'],
      trim: true,
    },
    lastName: {
      type: String,
      required: [true, 'Please enter your last name.'],
      trim: true,
    },
    email: {
      type: String,
      required: [true, 'An email address is required.'],
      unique: true,
      lowercase: true,
      validate: [validator.isEmail, 'Please eneter a valid email address.'],
    },
    phone: {
      type: String,
      required: [true, 'A phone number is reuired.'],
      validate: [
        validator.isMobilePhone,
        'Please eneter a valid phone number.',
      ],
    },
    password: {
      type: String,
      required: [true, 'Please enter your password.'],
      minlength: [8, 'Password should be at least 8 characters long.'],
      select: false,
    },
    passwordConfirm: {
      type: String,
      required: [true, 'Please confirn your password.'],
      validate: {
        validator: function (passwordConfirm) {
          return passwordConfirm === this.password;
        },
        message: 'Passwords are not the same.',
      },
    },
    dlNumber: {
      type: Number,
      required: [true, 'Please enter the DL number.'],
    },
    expirationDate: {
      type: Date,
      required: [true, 'Please enter the expiration number.'],
    },
    state: {
      type: String,
      required: [true, 'Please specify the state.'],
    },
    isVerified: {
      type: Boolean,
      default: false,
    },
    isActive: {
      type: Boolean,
      default: true,
      select: false,
    },
    createdAt: {
      type: Date,
      default: Date.now(),
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    responseTime: String,
    numberOfReports: Number,
    responseRate: String,
    profilPicture: String,
    passwordResetToken: String,
    verificationToken: String,
    passwordChangetAt: Date,
    passwordResetExpires: Date,
    refreshToken: String,
    // relations
    subscription: {
      type: Schema.Types.ObjectId,
      ref: 'Subscription',
    },
    properties: [
      {
        type: Schema.Types.ObjectId,
        ref: 'Property',
      },
    ],
    payments: [
      {
        type: Schema.Types.ObjectId,
        ref: 'HostPayment',
      },
    ],
  },
  {
    toJSON: { virtuals: true },
    toObject: { virtuals: true },
  }
);

// pre save middlewaree for changing passwordChangetAt field to the date where password is changed
hostSchema.pre('save', function (next) {
  if (!this.isModified('password') || this.isNew) return next();

  this.passwordChangetAt = (Date.now() - 1000) as any;
  next();
});

// pre save middleware for hashing and persisting the hashed password to the database
hostSchema.pre('save', async function (next) {
  if (!this.isModified('password')) return next();

  const password = await bcrypt.hash(this.password, 12);
  this.password = password;
  this.passwordConfirm = undefined;

  next();
});

hostSchema.pre('save', async function (next) {
  const token: string = uuidv4();
  const refreshToken = await bcrypt.hash(token, 8);
  this.refreshToken = refreshToken;

  next();
});

hostSchema.methods.checkPassword = async function (
  candidatePassword: string,
  password: string
) {
  return bcrypt.compareSync(candidatePassword, password);
};

hostSchema.methods.passwordChangetAfter = function (JwtTimestamp: number) {
  if (this.passwordChangetAt) {
    const changedTimestamp = parseInt(
      (this.passwordChangetAt.getTime() / 1000) as any,
      10
    );
    return JwtTimestamp < changedTimestamp;
  }
  return false;
};

hostSchema.methods.createPasswordResetToken = async function () {
  const resetToken = await signOrEncryptTokens();

  this.passwordResetToken = resetToken.hashedToken;
  this.passwordResetExpires = Date.now() + 10 * 60 * 1000;

  return resetToken.token;
};

hostSchema.methods.createverifyToken = async function () {
  const verifyToken = await signOrEncryptTokens();

  this.verificationToken = verifyToken.hashedToken;

  return verifyToken.token;
};

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const Host: IHostModel = mongoose.model<IHostDocument, IHostModel>(
  'Host',
  hostSchema
);
export default Host;
