/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
import { eStatusCode } from '@nursesrent/libs/shared';
import { Model } from 'mongoose';
import { ApiFeatures, AppError, catchAsync, checkId } from '../utils';
import * as CONST from '../utils';
import * as LABELS from '../utils';

/**
 *
 * @param  Model Mongoose model
 * @returns 204 noc content status code and no data
 */
const deleteOne = <T = any>(Model: Model<T>) =>
  catchAsync(async (req, res, next) => {
    const { id } = req.params;
    checkId(id, next);

    const doc = await Model.findByIdAndDelete(id);

    if (!doc) {
      return next(new AppError(LABELS.NO_DOC_FOUND, eStatusCode.NOT_FOUND));
    }

    res.status(eStatusCode.NO_CONTENT).json({
      status: CONST.STATUS.SUCCESS,
      data: null,
    });
  });

/**
 *
 * @param  Model Mongoose model
 * @returns 200 Ok status code and updated data
 */
const updateOne = <T = any, Dto = any>(Model: Model<T>) =>
  catchAsync(async (req, res, next) => {
    const { id } = req.params;
    checkId(id, next);

    const doc: Dto = await Model.findByIdAndUpdate(id, req.body, {
      new: true,
      runValidators: true,
    });

    if (!doc) {
      return next(new AppError(LABELS.NO_DOC_FOUND, eStatusCode.NOT_FOUND));
    }

    res.status(eStatusCode.OK).json({
      status: CONST.STATUS.SUCCESS,
      data: doc,
    });
  });

/**
 *
 * @param Model Mongoose model
 * @returns 201 created ststus code and created data
 */
const createOne = <T = any, Dto = any>(Model: Model<T>) =>
  catchAsync(async (req, res, next) => {
    const doc = (await Model.create(req.body)) as Dto;

    res.status(eStatusCode.CREATED).json({
      status: CONST.STATUS.SUCCESS,
      data: doc,
    });
  });

/**
 *
 * @param Model Mongoose model
 * @param popOptions population options
 * @returns 200 ok status code and founded data
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const getOne = <Dto = any>(Model: Model<any>, popOptions?: any) =>
  catchAsync(async (req, res, next) => {
    const { id } = req.params;
    checkId(id, next);

    let query = Model.findById(id);
    if (popOptions) query = query.populate(popOptions);
    const doc: Dto = await query;

    if (!doc) {
      return next(new AppError(LABELS.NO_DOC_FOUND, eStatusCode.NOT_FOUND));
    }

    res.status(eStatusCode.OK).json({
      status: CONST.STATUS.SUCCESS,
      data: doc,
    });
  });

/**
 *
 * @param Model Mongoose model
 * @returns 200 ok status code and all the queried data
 */
const getAll = <T = any, Dto = any>(Model: Model<T>) =>
  catchAsync(async (req, res, next) => {
    const features = new ApiFeatures(Model.find(), req.query)
      .filter()
      .sort()
      .limitFields()
      .paginate();
    const doc: Dto[] = await features.query;

    // SEND RESPONSE
    res.status(eStatusCode.OK).json({
      status: CONST.STATUS.SUCCESS,
      results: doc.length,
      data: doc,
    });
  });

const handlerFactory = {
  getAll,
  getOne,
  createOne,
  updateOne,
  deleteOne,
};

export default handlerFactory;
